import React, { useContext, useState } from "react";
import { Info, Settings } from "@mui/icons-material";
import { Backdrop, Fab, FormControlLabel, FormGroup, Switch, Tooltip } from "@mui/material";

import { UiCard } from "../UiCard";
import { UiSettingsContext } from "./UiSettingsContext";

export const UiSettings = () => {
    const [showSettings, setShowSettings] = useState(false);
    const { darkMode, slackMode, toggleDarkMode, toggleSlackMode } = useContext(UiSettingsContext);

    const toggleSettings = () => setShowSettings(!showSettings);

    return (
        <>
            <Fab
                color="primary"
                sx={{
                    position: 'absolute',
                    bottom: (theme) => theme.spacing(2),
                    right: (theme) => theme.spacing(2),
                }}
                onClick={toggleSettings}
            >
                <Settings />
            </Fab>
            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={showSettings}
                onClick={toggleSettings}>
                <UiCard title='Settings'>
                    <FormGroup onClick={e => e.stopPropagation()}>
                        <FormControlLabel control={
                            <Switch checked={darkMode} onChange={toggleDarkMode} />
                        } label="Dark Mode" />
                        <FormControlLabel control={
                            <Switch checked={slackMode} onChange={toggleSlackMode} />
                        } label={<div>Slack Mode<Tooltip title="Enter custom emoji names"><Info fontSize="small" /></Tooltip></div>} />

                    </FormGroup>
                </UiCard>
            </Backdrop>
        </>
    );
};