import React, { useState } from 'react';
import { ContentCopy } from '@mui/icons-material';
import { Alert, Button, Collapse, Stack, Typography } from '@mui/material';
import { UiCard } from '../UiCard';

interface WordleOutputProps {
    /**
     * What wordle to start with
     */
    wordle: string;
}

export const WordleOutput = ({
    wordle,
}: WordleOutputProps) => {
    const [showToast, setShowToast] = useState(false);

    const copyToClipboard = () => {
        navigator.clipboard.writeText(wordle);
        setShowToast(true);
        window.setTimeout(() => {
            setShowToast(false);
        }, 5000);
    };

    return (
        <UiCard title="Converted Wordle">
            <Stack>
                <div style={{ padding: '20px', whiteSpace: 'pre-wrap' }}>
                    <Typography align="center">{wordle}</Typography>
                </div>
                <Button sx={{ margin: 1 }} variant='contained' onClick={copyToClipboard} startIcon={<ContentCopy />}>
                    Copy
                </Button>
                <Collapse in={showToast}>
                    <Alert severity="info">Copied converted Wordle to clipboard!</Alert>
                </Collapse>
            </Stack>
        </UiCard>
    );
};