import React, { useEffect, useState } from 'react';
import {
  Box,
  Container,
  createTheme,
  CssBaseline,
  Stack,
  ThemeProvider,
  Typography,
} from '@mui/material';

import { EmojiDropdown } from './components/EmojiDropdown/EmojiDropdown';
import { WordleInput } from './components/WordleInput/WordleInput';
import { WordleOutput } from './components/WordleOutput/WordleOutput';
import { UiSettings } from './components/UiSettings/UiSettings';
import { UiSettingsContext } from './components/UiSettings/UiSettingsContext';

const WRONG_WORDLE_EMOJI_REGEX = /⬛|⬜/gu;
const INCLUDED_WORDLE_EMOJI_REGEX = /🟨|🟦/gu;
const CORRECT_WORDLE_EMOJI_REGEX = /🟩|🟧/gu;

const DEFAULT_WRONG_EMOJI = "🖤";
const DEFAULT_INCLUDED_EMOJI = "💛";
const DEFAULT_CORRECT_EMOJI = "💚";
const DEFAULT_WRONG_SLACK_EMOJI = "black_heart";
const DEFAULT_INCLUDED_SLACK_EMOJI = "yellow_heart";
const DEFAULT_CORRECT_SLACK_EMOJI = "green_heart";

const DEFAULT_WORDLE = `Wordle 215 6/6

🟨🟨⬛⬛⬛
⬛🟨⬛🟩🟨
🟩⬛⬛🟩⬛
🟩🟩🟩⬛⬛
🟩🟩⬛⬛🟩
🟩🟩🟩🟩🟩`;

function App() {
  const storedDarkMode = localStorage.getItem('darkMode');
  const storedSlackMode = localStorage.getItem('slackMode');
  const [darkMode, setDarkMode] = useState(storedDarkMode === 'true');
  const [slackMode, setSlackMode] = useState(storedSlackMode === 'true');

  const [wordle, setWordle] = useState(DEFAULT_WORDLE);
  const [convertedWordle, setConvertedWordle] = useState(DEFAULT_WORDLE);
  const [wrongEmoji, setWrongEmojiEmoji] = useState(DEFAULT_WRONG_EMOJI);
  const [includedEmoji, setIncludedEmojiEmoji] = useState(DEFAULT_INCLUDED_EMOJI);
  const [correctEmoji, setCorrectEmojiEmoji] = useState(DEFAULT_CORRECT_EMOJI);
  const [wrongSlackEmoji, setWrongSlackEmoji] = useState(DEFAULT_WRONG_SLACK_EMOJI);
  const [includedSlackEmoji, setIncludedSlackEmoji] = useState(DEFAULT_INCLUDED_SLACK_EMOJI);
  const [correctSlackEmoji, setCorrectSlackEmoji] = useState(DEFAULT_CORRECT_SLACK_EMOJI);

  useEffect(() => {
    const replacedWrongWordle = wordle.replace(WRONG_WORDLE_EMOJI_REGEX, slackMode ? `:${wrongSlackEmoji}:` : wrongEmoji);
    const replacedIncludedWordle = replacedWrongWordle.replaceAll(INCLUDED_WORDLE_EMOJI_REGEX, slackMode ? `:${includedSlackEmoji}:` : includedEmoji);
    const replacedCorrectWordle = replacedIncludedWordle.replaceAll(CORRECT_WORDLE_EMOJI_REGEX, slackMode ? `:${correctSlackEmoji}:` : correctEmoji);

    setConvertedWordle(replacedCorrectWordle);
  },
    [wordle, wrongEmoji, includedEmoji, correctEmoji, slackMode, wrongSlackEmoji, includedSlackEmoji, correctSlackEmoji]);

  const toggleDarkMode = () => {
    localStorage.setItem('darkMode', (!darkMode).toString());
    setDarkMode(!darkMode);
  };
  const toggleSlackMode = () => {
    localStorage.setItem('slackMode', (!slackMode).toString());
    setSlackMode(!slackMode);
  };

  const setWrongEmoji = (emoji: string) => slackMode ? setWrongSlackEmoji(emoji) : setWrongEmojiEmoji(emoji);
  const setIncludedEmoji = (emoji: string) => slackMode ? setIncludedSlackEmoji(emoji) : setIncludedEmojiEmoji(emoji);
  const setCorrectEmoji = (emoji: string) => slackMode ? setCorrectSlackEmoji(emoji) : setCorrectEmojiEmoji(emoji);

  const theme = createTheme({ palette: { mode: darkMode ? 'dark' : 'light' } });

  return (
    <UiSettingsContext.Provider value={{
      darkMode,
      slackMode,
      toggleDarkMode,
      toggleSlackMode,
    }}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <main>
          <Box sx={{ bgcolor: 'background.paper' }}>
            <Container maxWidth="sm">
              <Typography
                component="h1"
                variant="h2"
                align="center"
                color="text.primary"
                gutterBottom
              >
                Wordle Converter
              </Typography>
              <Typography variant="h5" align="center" color="text.secondary" paragraph>
                Squares got you down?
                Try out a new emoji layout for sharing your daily wordle results.
              </Typography>
            </Container>
          </Box>
          <Container sx={{ py: 8 }}>
            <Stack
              direction={{ xs: 'column', md: 'row' }}
              spacing={{ xs: 1, sm: 2, md: 4 }}
              justifyContent="space-evenly"
              alignItems="center"
            >
              <WordleInput defaultWordle={DEFAULT_WORDLE} onChange={setWordle} />
              <Stack direction='column' spacing={{ xs: 1, sm: 2, md: 4 }}>
                <EmojiDropdown label="Wrong Letter" defaultEmoji={DEFAULT_WRONG_EMOJI} defaultSlackEmoji={DEFAULT_WRONG_SLACK_EMOJI} onChange={setWrongEmoji} />
                <EmojiDropdown label="Included Letter" defaultEmoji={DEFAULT_INCLUDED_EMOJI} defaultSlackEmoji={DEFAULT_INCLUDED_SLACK_EMOJI} onChange={setIncludedEmoji} />
                <EmojiDropdown label="Correct Letter" defaultEmoji={DEFAULT_CORRECT_EMOJI} defaultSlackEmoji={DEFAULT_CORRECT_SLACK_EMOJI} onChange={setCorrectEmoji} />
              </Stack>
              <WordleOutput wordle={convertedWordle} />
            </Stack>
          </Container>
          <UiSettings />
        </main>
      </ThemeProvider>
    </UiSettingsContext.Provider>
  );
}

export default App;
